.hero-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: absolute;
    /* margin-top: 1rem; */
    top: 0;
    z-index: 10;
    padding: 1rem 2rem;
    align-items: center;
    color: grey;   
}

.nav-links {
    display: flex;
    gap: 20px;
    list-style: none;
}

.hamburger-icon {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    z-index: 20;
}
.link-style{
    color: gray;
}
.svg-container {
    display: flex;
    justify-content: center; 
    align-items: center;
  }
  
  .svg-icon {
    width: 80px; 
    height: 80px; 
    margin-right: 100px;  
}
  

@media (max-width: 768px) {
    .hamburger-icon {
        display: block;
        margin-left: 10px;
    }

    .nav-links {
        display: none;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.8);
        position: absolute;
        width: 20rem;
        padding: 1rem;
        z-index: 10;
    }

    .nav-links.open {
        display: flex;
        margin-top: 7rem;
        width: 80%;
    }
}

.hero-nav ul {
    gap: 20px;
    list-style: none;
}

.hero-nav ul li {
    cursor: pointer;
    font-size: 1rem;
    font-family: 'Courier New', Courier, monospace;
}

.social-icons {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-right: 10px;
}

.switch-icon {
    text-decoration: none;
    font-size: small;
    /* width: 7em; */
    color: white;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    text-align: center;
    border: 1px solid black;
    font-size: smaller;
}
